<template>
  <el-form ref="form" :model="formData" label-width="80px" style="width:400px;margin:auto;margin-top:50px">
    <el-form-item label="">
      <el-switch v-model="is_show"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="formData.emer_title" placeholder="(30)" maxlength="30"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="formData.emer_content" type="textarea" :rows="3" placeholder="(255)"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit" :loading="loading"></el-button>
    </el-form-item>
  </el-form>
</template>
<script>
export default require('./emer.js')
</script>



