<template>
  <div>
    <div class="panel-between">
      <div class="o-left">
        
        
        
        <div class="panel-start">
          <div style="width:203px;margin-right:20px">
            <div class="ma-t30"></div>
            <el-table
              :data="msg.orderType"
              ref="multipleTable"
              tooltip-effect="dark"
              border
              size="small"
              style="width: 100%;margin-top:8px"
            >
              <el-table-column prop="title" label=""></el-table-column>
              <el-table-column prop="total" label=""></el-table-column>
            </el-table>
          </div>
          <div style="width:203px;margin-right:20px">
            <div class="ma-t30"></div>
            <el-table
              :data="msg.orderTypeMonth"
              ref="multipleTable"
              tooltip-effect="dark"
              border
              size="small"
              style="width: 100%;margin-top:8px"
            >
              <el-table-column prop="title" label=""></el-table-column>
              <el-table-column prop="total" label=""></el-table-column>
            </el-table>
          </div>
          <div style="width:203px;margin-right:20px">
            <div class="ma-t30"></div>
            <el-table
              :data="msg.jdorder"
              ref="multipleTable"
              tooltip-effect="dark"
              border
              size="small"
              style="width: 100%;margin-top:8px"
            >
              <el-table-column prop="state" label="">
                <template slot-scope="scope">
                  <div>{{scope.row.state == 0?'':scope.row.state == 1?'':scope.row.state == 2?'':scope.row.state == 3?'':''}}</div>
                </template>
              </el-table-column>
              <el-table-column prop="total" label=""></el-table-column>
            </el-table>
          </div>
          <div style="width:203px;margin-right:20px">
            <div class="ma-t30"></div>
            <el-table
              :data="msg.jdorderDaily"
              ref="multipleTable"
              tooltip-effect="dark"
              border
              size="small"
              style="width: 100%;margin-top:8px"
            >
              <el-table-column prop="state" label="">
                <template slot-scope="scope">
                  <div>{{scope.row.state == 0?'':scope.row.state == 1?'':scope.row.state == 2?'':scope.row.state == 3?'':''}}</div>
                </template>
              </el-table-column>
              <el-table-column prop="total" label=""></el-table-column>
            </el-table>
          </div>
        </div>
      </div>
      
    </div>
  </div>
</template>
<script>
export default require("./wxview.js");
</script>



