<template>
  <el-form
    ref="form"
    :model="formData"
    label-width="80px"
    style="width:400px;margin:auto;margin-top:50px"
  >
    <el-form-item label="">
      <el-input v-model="formData.username" placeholder="4"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="formData.password" type="password" placeholder="8"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="formData.checkPwd" type="password" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-select v-model="formData.dtype" placeholder="" style="width:100%">
        <el-option v-for="item in utype" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="" v-if="formData.dtype == 2">
      <el-input v-model="formData.a_id" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="formData.phone" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="" v-if="formData.dtype == 2">
      <el-date-picker v-model="formData.deadline" style="width:100%" type="date" placeholder=""></el-date-picker>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit" :loading="loading"></el-button>
    </el-form-item>
  </el-form>
</template>
<script>
export default require("./agent_edit.js");
</script>



