<template>
  <div>
    <el-form
      ref="form"
      :model="formData"
      label-width="90px"
      style="width:400px;margin:auto;margin-top:50px"
    >
      <el-form-item label="">
        <el-select
          v-model="formData.server_name"
          placeholder=""
          style="width:100%"
          :disabled="dtype != 1"
        >
          <el-option
            v-for="item in utype"
            :key="item.label"
            :label="item.label"
            :value="item.label"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" v-if="formData.server_name == ''">
        <el-row :gutter="0">
          <el-col :span="8">
            <el-input v-model="price[0]" placeholder=""></el-input>
          </el-col>
          <el-col :span="8">
            <el-input v-model="price[1]" placeholder=""></el-input>
          </el-col>
          <el-col :span="8">
            <el-input v-model="price[2]" placeholder=""></el-input>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item label="" v-if="formData.server_name == ''">
        <el-row :gutter="0">
          <el-col :span="8">
            <el-input v-model="price[0]" placeholder=""></el-input>
          </el-col>
          <el-col :span="8">
            <el-input v-model="price[1]" placeholder=""></el-input>
          </el-col>
          <el-col :span="8">
            <el-input v-model="price[2]" placeholder=""></el-input>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item label="ID">
        <el-input v-model="formData.dl_id" placeholder="ID" :disabled="dtype != 1 "></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          v-model="formData.user_sy"
          type="number"
          placeholder=""
          :disabled="dtype != 1 "
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.dl_sy" type="number" placeholder="" :disabled="dtype != 1 "></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.p_sy" type="number" placeholder="" :disabled="dtype != 1 "></el-input>
      </el-form-item>
      <el-form-item
        label=""
        v-if="formData.server_name == '' || formData.server_name == '' || formData.server_name == ''"
      >
        <el-tag
          :key="tag"
          v-for="tag in dynamicTags"
          closable
          :disable-transitions="false"
          @close="handleClose(tag)"
        >{{tag}}</el-tag>
        <el-input
          class="input-new-tag"
          v-if="inputVisible"
          v-model="inputValue"
          ref="saveTagInput"
          size="small"
          @keyup.enter.native="handleInputConfirm"
          @blur="handleInputConfirm"
        ></el-input>
        <el-button v-else class="button-new-tag" size="small" @click="showInput">+ </el-button>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.des" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="ICON">
        <el-input v-model="formData.icon" placeholder="ICON" :disabled="dtype != 1 "></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-button @click="dialogVisible = true"></el-button>
        <div class="ma-t20 panel-start item-center" v-if="jdr.length > 1">
          <img :src="jdr[0]" width="50px">
          <div class="ma-l20">{{jdr[1]}}</div>
        </div>
      </el-form-item>
      <el-form-item label="">
        <el-switch v-model="formData.is_show" :active-value="1" :inactive-value="0"></el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit" :loading="loading"></el-button>
      </el-form-item>
    </el-form>
    <el-dialog title="" :visible.sync="dialogVisible" width="900px">
      <jd></jd>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="getJd(),dialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<style>
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
</style>
<script>
import jd from "../wxuser/jdlist/jdlist.vue";
let app = require("./server.js");
app.components = { jd };
export default app;
</script>



